
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.simple-description {
  width: 500px;
  margin: 10px;
  color: grey;
}
.title-text {
  width: 100%;
  border-top: 5px solid #ebf5fe;
  border-bottom: 5px solid #ebf5fe;
  font-size: 20px;
  font-weight: 600;
  color: #101b9c;
}
.preamble {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.instructions {
  width: 500px;
  text-align: left;
}
.instructions li {
  margin-bottom: 5px;
}
.textgrid-processor {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.displayarea {
  width: 1000px;
  height: 400px;
  border: 1px solid black;
}
.notice {
  margin-top: 5px;
}

.container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border: 2px solid #6885b7;
  padding: 12px;
  margin: 12px;
  border-radius: 36px;
  min-width: 720px;
}
.column {
  margin: 20px;
}
.table-column-header {
  font-weight: 600;
  margin-bottom: 5px;
}
.table-column-content {
  text-align: left;
}
.filename-textbox {
  margin-top: 25px;
  display: block;
  width: 180px;
}
.download-button {
  height: 30px;
  width: auto;
  margin: 25px;
  border-radius: 5px;
  background-color: #F0F0F0;
  border: 1px solid black;
}
.download-button:hover {
  background-color: #6d98e6
}
.download-button:active {
  background-color: #B0B0B0;
}








































.dropzone {
    height: 100px;
    width: 200px;
    border: 3px dashed black;
    background-color: #c5d4ef;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    margin: 16px;
}
